
.landing-body {
    -webkit-transition: radial-gradient 1s ease-in-out;
    -o-transition: radial-gradient 1s ease-in-out;
    transition: radial-gradient 1s ease-in-out;
}

body.hidden-content > * {
    visibility: hidden;
    opacity: 0;
}

.container {
    width: 1720px;
    max-width: 100%;
    padding: 0 45px;
    margin: 0 auto;
    height: 700px;
    min-height: 100vh;
    position: relative;
    overflow: hidden;
}

.socials-list {
    &__item {
        position: relative;
        vertical-align: middle;

        svg path {
            transition: .3s ease;
        }
    }
    &__icon:hover {
        svg path {
            fill: $accent-color;
        }
    }
    li + li {
        margin-left: 32px;
    }
}

.content {
    display: flex;
    height: 100%;

    &__section {
        position: relative;
        z-index: 2;
        display: flex;
        flex-direction: column;
        justify-content: center;
        max-width: 45%;
    }

    &__title {
        font-size: 72px;
        line-height: 1.18;
        max-width: 848px;
        margin-bottom: 32px;
        margin-top: 0;
    }

    &__subtitle {
        font-size: 18px;
        line-height: 1.77;
        max-width: 720px;
        margin-bottom: 36px;
    }
}

.decor {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: -1;

    img {
        max-width: none;
        position: absolute;
        top: 50%;
        right: -245px;
        transform: matrix(1, 0, 0, 1, 0, -705);
        -webkit-transform: matrix(1, 0, 0, 1, 0, -705);
        margin-top: 115px;
        max-height: 1410px;
    }
}

.letter {
    display: inline-block;
    min-width: 20px;
}

#circle {
    position: absolute;
    top: 0;
    left: 0;
    width: 800px;
    height: 800px;
    z-index: -1;
    background: radial-gradient(closest-side, rgba(59,187,233, .7), $bg-color)
}

.c-subscribe {
    position: relative;

    &__form {
        display: flex;
        align-items: flex-start;
        flex-wrap: nowrap;
        flex-direction: row;
    }

    &__inputs {
        max-width: 100%;
        width: 100%;
        flex: 0 0 80%;
        overflow: hidden;
        min-height: 100px;
    }

    &__submit {
        position: relative;
        margin-left: 24px;

        .c-button {
            position: relative;
            overflow: hidden;
            min-width: inherit;
            width: 180px;
            transition: width .4s ease-in-out;
        }
        .c-icon {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%) scale(0);
            margin-left: 2px;
            transition: transform .4s ease;
            width: 25px;
            height: 25px;
        }
    }

    &__success {
        color: #fff;
        position: absolute;
        left: -9999px;
        top: 0;
        padding-top: 5px;
        font-size: 18px;
        font-weight: 700;

        &-title {
            line-height: 1;
            position: relative;
            color: $accent-color;
            opacity: 0;
            transform: translateY(5px);
            position: relative;
            opacity: 0;
            -webkit-transform: translateY(5px);
            transform: translateY(5px);
            -webkit-transition-property: opacity, -webkit-transform;
            transition-property: opacity, -webkit-transform;
            transition-property: opacity, transform;
            transition-property: opacity, transform, -webkit-transform;
            -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s;
            -webkit-transition-timing-function: ease-in-out;
            transition-timing-function: ease-in-out;
            margin-bottom: 0;
        }

        &-subtitle {
            line-height: 2;
            position: relative;
            opacity: 0;
            transform: translateY(5px);
            -webkit-transform: translateY(5px);
            transform: translateY(5px);
            -webkit-transition-property: all;
            transition-property: all;
            -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s;
            -webkit-transition-timing-function: ease-in-out;
            transition-timing-function: ease-in-out;
            margin-bottom: 0;
        }
    }

    &.has-success {

        .c-subscribe__form {
            width: 100%;
        }

        .c-subscribe__inputs {
            -webkit-transition-delay: .6s;
            transition-delay: .6s;
            -webkit-box-flex: 0;
            -ms-flex: 0 0 0;
            flex: 0 0 0;
            height: 0;
            -webkit-transform: scale(0);
            transform: scale(0);
            -webkit-transition-property: all;
            transition-property: all;
            -webkit-transition-duration: 0.4s;
            transition-duration: 0.4s;
            -webkit-transition-timing-function: ease-in-out;
            transition-timing-function: ease-in-out;

            .с-input-group {
                -webkit-transition-delay: .1s;
                transition-delay: .1s;
            }

            .c-checkbox, .с-input-group {
                -webkit-transform: translateY(15px);
                transform: translateY(15px);
                opacity: 0;
                -webkit-transition-property: all;
                transition-property: all;
                -webkit-transition-duration: 0.2s;
                transition-duration: 0.2s;
                -webkit-transition-timing-function: ease-in-out;
                transition-timing-function: ease-in-out;
            }
        }

        .c-subscribe__success {
            left: 72px;
        }

        .c-subscribe__success-title {
            transition-delay: 1s;
            transform: translateY(0);
            opacity: 1;
        }

        .c-subscribe__success-subtitle {
            transition-delay: 1.1s;
            transform: translateY(0);
            opacity: 1;
        }

        .c-subscribe__submit .c-button {
            width: 56px;
            flex: 0 0 56px;
            pointer-events: none;
            padding-right: 8px;
            padding-left: 8px;
        }

        .c-subscribe__submit {
            width: 100%;
            max-width: 100%;
            -webkit-box-flex: 0;
            -ms-flex: 0 0 100%;
            flex: 0 0 100%;
            margin-right: 16px;
        }

        .c-button__text {
            opacity: 0;
            position: relative;
            display: block;
            -webkit-transform: scaleX(0);
            transform: scaleX(0);
            -webkit-transition-property: all;
            transition-property: all;
            -webkit-transition-duration: 0.4s;
            transition-duration: 0.4s;
            -webkit-transition-timing-function: ease-in-out;
            transition-timing-function: ease-in-out;
        }

        .c-subscribe__submit .c-button .c-icon {
            -webkit-transform: translate(-50%, -50%) scale(1);
            transform: translate(-50%, -50%) scale(1);
            -webkit-transition-delay: .2s;
            transition-delay: .2s;
        }
    }

}
