@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Rubik:400,500,700");
.list-unstyled { list-style: none; padding: 0; margin: 0; }

.list-inline li { display: inline-block; }

.u-accent-color.active { color: #3bbbe9 !important; }

.u-second-color.active { color: #ff1967 !important; }

html { -webkit-box-sizing: border-box; box-sizing: border-box; height: 100%; }

body { margin: 0; font-family: "Rubik", sans-serif; color: #fff; background-color: #010e28; font-size: 14px; line-height: 1.2; min-height: 100%; width: 100%; position: relative; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; overflow-x: hidden; min-width: 320px; }

*, ::after, ::before { box-sizing: border-box; -webkit-box-sizing: border-box; }

p { padding: 0; margin-top: 0; }

a { color: inherit; outline: none; }

a:hover { text-decoration: none; color: inherit; }

a:focus { text-decoration: none; color: inherit; }

div, ul, li, button, input, textarea { outline: none; }

fieldset { border: 0; margin: 0 0 10px; padding: 0; }

button { padding: 0; margin: 0; }

input, button { border-radius: 0; }

h1, h2, h3, h4, h5, h6 { color: #fff; }

ul, ol { list-style: none; margin: 0; padding: 0; }

input, textarea { padding: 0 10px; border-radius: 0; }

textarea { padding: 10px; }

textarea:focus::placeholder { opacity: 0; text-indent: 10px; }

input:focus::placeholder { opacity: 0; text-indent: 10px; }

img { max-width: 100%; height: auto; vertical-align: middle; }

.c-input { background-color: #fff; border: 1px solid #ddd; border-radius: 8px; height: 56px; font-size: 14px; color: #718792; width: 100%; transition: .3s ease; padding: 0 16px; }

.c-input::placeholder { font-size: 14px; color: #718792; transition: .3s ease; }

.c-input:focus { border-color: #718792; }

.c-input:focus::placeholder { text-indent: 10px; opacity: 0; }

@media (max-width: 1200px) { .c-input { font-size: 16px; } }

.c-button { display: inline-block; font-size: 14px; font-weight: 700; color: #fff; border: none; min-width: 180px; min-height: 56px; border-radius: 56px; text-align: center; outline: none; cursor: pointer; }

.c-button--pink { background-color: #ff1967; }

.c-button--pink:active, .c-button--pink:hover { background-color: #FF186A; }

.c-checkbox { position: relative; cursor: pointer; vertical-align: middle; min-height: 16px; display: inline-flex; }

.c-checkbox__text { font-size: 12px; line-height: 1.33; position: relative; padding-top: 4px; }

.c-checkbox__icon { position: relative; line-height: 0; fill: #fff; background-color: #ddd; border-radius: 2px; flex: 0 0 20px; max-width: 20px; display: flex; justify-content: center; align-items: center; height: 20px; width: 20px; margin-right: 16px; transition: .3s ease; }

.c-checkbox__icon .c-icon { display: block; opacity: 0; visibility: hidden; transform: scale(0); transition: .3s ease; }

.c-checkbox__input { display: none; }

.c-checkbox__input:checked ~ .c-checkbox__icon { background-color: #3bbbe9; }

.c-checkbox__input:checked ~ .c-checkbox__icon .c-icon { visibility: visible; opacity: 1; transform: scale(1); }

.с-input-group { width: 100%; }

.с-input-group__message { display: block; visibility: hidden; opacity: 0; font-size: 12px; line-height: 1.33; min-height: 16px; margin-top: 2px; margin-bottom: 6px; transition: .3s ease; }

.с-input-group.has-error .с-input-group__message { color: #ff1919; visibility: visible; opacity: 1; }

.с-input-group.has-error .c-input { border-color: #ff1919; color: #ff1919; }

.footer { font-size: 18px; line-height: 1.77; font-weight: 500; z-index: 10; position: absolute; bottom: 0; width: 1720px; max-width: 100%; padding: 0 45px 30px; margin: 0 auto; left: 50%; transform: translateX(-50%); }

.footer__title { max-width: 400px; }

.footer p { margin-bottom: 0; }

.footer a { color: #3bbbe9; text-decoration: none; }

.landing-body { -webkit-transition: radial-gradient 1s ease-in-out; -o-transition: radial-gradient 1s ease-in-out; transition: radial-gradient 1s ease-in-out; }

body.hidden-content > * { visibility: hidden; opacity: 0; }

.container { width: 1720px; max-width: 100%; padding: 0 45px; margin: 0 auto; height: 700px; min-height: 100vh; position: relative; overflow: hidden; }

.socials-list__item { position: relative; vertical-align: middle; }

.socials-list__item svg path { transition: .3s ease; }

.socials-list__icon:hover svg path { fill: #3bbbe9; }

.socials-list li + li { margin-left: 32px; }

.content { display: flex; height: 100%; }

.content__section { position: relative; z-index: 2; display: flex; flex-direction: column; justify-content: center; max-width: 45%; }

.content__title { font-size: 72px; line-height: 1.18; max-width: 848px; margin-bottom: 32px; margin-top: 0; }

.content__subtitle { font-size: 18px; line-height: 1.77; max-width: 720px; margin-bottom: 36px; }

.decor { position: absolute; top: 0; right: 0; width: 100%; height: 100%; overflow: hidden; z-index: -1; }

.decor img { max-width: none; position: absolute; top: 50%; right: -245px; transform: matrix(1, 0, 0, 1, 0, -705); -webkit-transform: matrix(1, 0, 0, 1, 0, -705); margin-top: 115px; max-height: 1410px; }

.letter { display: inline-block; min-width: 20px; }

#circle { position: absolute; top: 0; left: 0; width: 800px; height: 800px; z-index: -1; background: radial-gradient(closest-side, rgba(59, 187, 233, 0.7), #010e28); }

.c-subscribe { position: relative; }

.c-subscribe__form { display: flex; align-items: flex-start; flex-wrap: nowrap; flex-direction: row; }

.c-subscribe__inputs { max-width: 100%; width: 100%; flex: 0 0 80%; overflow: hidden; min-height: 100px; }

.c-subscribe__submit { position: relative; margin-left: 24px; }

.c-subscribe__submit .c-button { position: relative; overflow: hidden; min-width: inherit; width: 180px; transition: width .4s ease-in-out; }

.c-subscribe__submit .c-icon { position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%) scale(0); margin-left: 2px; transition: transform .4s ease; width: 25px; height: 25px; }

.c-subscribe__success { color: #fff; position: absolute; left: -9999px; top: 0; padding-top: 5px; font-size: 18px; font-weight: 700; }

.c-subscribe__success-title { line-height: 1; position: relative; color: #3bbbe9; opacity: 0; transform: translateY(5px); position: relative; opacity: 0; -webkit-transform: translateY(5px); transform: translateY(5px); -webkit-transition-property: opacity, -webkit-transform; transition-property: opacity, -webkit-transform; transition-property: opacity, transform; transition-property: opacity, transform, -webkit-transform; -webkit-transition-duration: 0.3s; transition-duration: 0.3s; -webkit-transition-timing-function: ease-in-out; transition-timing-function: ease-in-out; margin-bottom: 0; }

.c-subscribe__success-subtitle { line-height: 2; position: relative; opacity: 0; transform: translateY(5px); -webkit-transform: translateY(5px); transform: translateY(5px); -webkit-transition-property: all; transition-property: all; -webkit-transition-duration: 0.3s; transition-duration: 0.3s; -webkit-transition-timing-function: ease-in-out; transition-timing-function: ease-in-out; margin-bottom: 0; }

.c-subscribe.has-success .c-subscribe__form { width: 100%; }

.c-subscribe.has-success .c-subscribe__inputs { -webkit-transition-delay: .6s; transition-delay: .6s; -webkit-box-flex: 0; -ms-flex: 0 0 0; flex: 0 0 0; height: 0; -webkit-transform: scale(0); transform: scale(0); -webkit-transition-property: all; transition-property: all; -webkit-transition-duration: 0.4s; transition-duration: 0.4s; -webkit-transition-timing-function: ease-in-out; transition-timing-function: ease-in-out; }

.c-subscribe.has-success .c-subscribe__inputs .с-input-group { -webkit-transition-delay: .1s; transition-delay: .1s; }

.c-subscribe.has-success .c-subscribe__inputs .c-checkbox, .c-subscribe.has-success .c-subscribe__inputs .с-input-group { -webkit-transform: translateY(15px); transform: translateY(15px); opacity: 0; -webkit-transition-property: all; transition-property: all; -webkit-transition-duration: 0.2s; transition-duration: 0.2s; -webkit-transition-timing-function: ease-in-out; transition-timing-function: ease-in-out; }

.c-subscribe.has-success .c-subscribe__success { left: 72px; }

.c-subscribe.has-success .c-subscribe__success-title { transition-delay: 1s; transform: translateY(0); opacity: 1; }

.c-subscribe.has-success .c-subscribe__success-subtitle { transition-delay: 1.1s; transform: translateY(0); opacity: 1; }

.c-subscribe.has-success .c-subscribe__submit .c-button { width: 56px; flex: 0 0 56px; pointer-events: none; padding-right: 8px; padding-left: 8px; }

.c-subscribe.has-success .c-subscribe__submit { width: 100%; max-width: 100%; -webkit-box-flex: 0; -ms-flex: 0 0 100%; flex: 0 0 100%; margin-right: 16px; }

.c-subscribe.has-success .c-button__text { opacity: 0; position: relative; display: block; -webkit-transform: scaleX(0); transform: scaleX(0); -webkit-transition-property: all; transition-property: all; -webkit-transition-duration: 0.4s; transition-duration: 0.4s; -webkit-transition-timing-function: ease-in-out; transition-timing-function: ease-in-out; }

.c-subscribe.has-success .c-subscribe__submit .c-button .c-icon { -webkit-transform: translate(-50%, -50%) scale(1); transform: translate(-50%, -50%) scale(1); -webkit-transition-delay: .2s; transition-delay: .2s; }

.header { padding: 48px 45px; display: flex; justify-content: space-between; align-items: center; position: absolute; top: 0; left: 50%; width: 1720px; transform: translateX(-50%); max-width: 100%; z-index: 10; }

.header__logo { display: inline-block; position: relative; max-width: 141px; }

.header__logo svg { display: block; width: 100%; }

@media (min-width: 1460px) { .container { position: static; }
  .content__section { padding-top: 0; } }

@media (min-width: 1920px) { .decor img { max-height: 1410px; } }

@media (min-width: 1921px) { .decor img { right: -40px; } }

@media (min-width: 1200px) { body { overflow: hidden; } }

@media (max-height: 650px) { body { overflow-y: auto; } }

@media (max-width: 1440px) { .decor img { max-height: 1250px; }
  .content__title { font-size: 52px; } }

@media (max-width: 1200px) { .decor img { max-height: inherit; max-width: 900px; } }

@media (max-width: 1024px) { .header { position: relative; padding: 48px 0; }
  .content__section { padding-top: 0; max-width: 44%; display: block; }
  .content__title { font-size: 32px; }
  .content__title span { font-size: 46px; }
  .content { display: block; height: auto; padding-top: 16px; }
  .decor img { margin-top: 208px; } }

@media (max-width: 980px) { .content__title { max-width: 540px; margin-bottom: 16px; }
  .content__section { max-width: 67%; }
  .content__subtitle { font-size: 20px; max-width: 650px; }
  .decor img { margin-top: 338px; }
  .c-subscribe__form { display: block; }
  .c-subscribe__submit { margin-left: 0; margin-top: 24px; } }

@media (max-width: 767px) { .container { height: auto; padding: 0 25px; }
  .header { padding: 24px 0; }
  .socials-list li + li { margin-left: 16px; }
  .header__logo { max-width: 100px; }
  .content__section { max-width: 100%; }
  .content__subtitle { font-size: 16px; }
  .decor img { max-height: none; max-width: 866px; top: inherit; bottom: -639px; right: -375px; transform: none !important; margin-top: 0; }
  #circle { display: none; }
  .footer { position: static; padding: 40px 0 24px; transform: none; left: 0; }
  .c-subscribe.has-success .c-subscribe__submit { display: flex; align-items: flex-start; }
  .c-subscribe__success { position: static; padding-left: 16px; } }

@media (max-width: 480px) { .content__title span { display: block; } }
